import React from 'react'
import {Link} from 'react-router-dom'
import ArchiveCategories from './archiveCategories'

export default class SubArticles2 extends React.Component{

    constructor(props){
        super(props);
        this.state = {
            subArticles: this.props
        }
    }

    render() {
        if (this.props.post) {
            let categories = Object.keys(this.props.post.categories)
            return (
                <div className="subArticles2" >
                    <div className="subImages2">
                        <Link to={`/post/${this.props.post.ID}`}>
                            <img src={this.props.post.featured_image} alt="featured articles" />
                        </Link>
                    </div>
                    <div className="subExcerpts2">
                        <div className="subExcerptsBox2">
                        <div>
                                <h4>{this.props.post.title}</h4>
                            </div>
                            <div>
                                <p dangerouslySetInnerHTML={{__html: this.props.post.excerpt}} />
                            </div>
                        </div>
                        <div className="subCategories2">
                            <ul>
                                {categories.map(category => <ArchiveCategories props={category} key={categories.indexOf(category)} />) }
                            </ul>            
                        </div>
                    </div>

                </div>
            )
        } else {
            return null
        }
        }
    

}