import React, { Component } from "react";
import ArchiveCategories from './archiveCategories'
// import { HashLink } from "react-router-hash-link";
import {Link} from 'react-router-dom'

export default class Article extends Component {
  removeUnicode(string) {
    if (string.indexOf("&#8217;") >= 0) {
      return this.removeUnicode(string.replace("&#8217;", "'"));
    } else {
      return string.replace("<p>", "").replace("[&hellip;]</p>", "...");
    }
  }

  render() {
    // const excerpt = this.removeUnicode(this.props.post.excerpt);
    if (this.props.post) {
      let categories = Object.keys(this.props.post.categories)
      return (
        <li key={this.props.post.ID}>
          <div className="archiveItem">
            <div className="archiveLeft">

            <div className="archiveNumber">
              <h1>{this.props.postNumber + 1}</h1>
            </div>
            <div className="archiveDescription">
              <div className="archiveUpperDescription">
                <div className="archiveTitle">
                  <Link to={`/post/${this.props.post.ID}`}>
                    <h1>{this.props.post.title}</h1>
                  </Link>
                </div>
                <div className="archiveCategories">
                  <ul>
                    {categories.map(category => <ArchiveCategories props={category} key={categories.indexOf(category)} />) }
                  </ul>            
                </div>
              </div>
              <div className="archiveExcerpt">
                <p dangerouslySetInnerHTML={{__html: this.props.post.excerpt}} />
              </div>
            </div>            
          </div>
            <div className="archiveImage">
              <img src={this.props.post.featured_image} alt="thumbnail" />
            </div>
        
          </div>
        </li>
        // <div className="anchor" id="archive" >
        // <p>archive placeholder</p>
        //   <h1 className="text-center">{this.props.post.title}</h1>
        //   <a href={"/blog/" + this.props.post.ID} className="blackLink">
        //     {this.props.post.featured_image ? (
        //       <img
        //         className="img-responsive webpic"
        //         alt="article header"
        //         src={this.props.post.featured_image}
        //       />
        //     ) : (
        //       ""
        //     )}
        //     <div className="content">{excerpt}</div>
        //   </a>
        //   {/* <HashLink to={"/blog/" + this.props.post.ID}>
        //     <button className="btn">Read More</button>
        //   </HashLink> */}
        // </div>
      );
    } else {
      return null;
    }
  }
}