import React from 'react'
import { Link } from 'react-router-dom'
import {connect} from 'react-redux'
import Tooltip from './tooltip'


const mapStateToProps = state => {
    return {
        posts: state
    }
}

class Header extends React.Component {

    constructor(props){
        super()
        this.exampleRef = React.createRef()
        this.handleScroll = this.handleScroll.bind(this)
        this.state = {scroll600: false};
        this.findLocationId = this.findLocationId.bind(this)
    }

    componentDidMount() {
        window.addEventListener('scroll', this.handleScroll);

    }

    componentWillUnmount(){
        window.removeEventListener('scroll', this.handleScroll);
      }

    handleScroll () {
            console.log(window.scrollY);
            if (window.scrollY > 605 && this.state.scroll600 === false){
                this.setState({
                    scroll600: true
                  });
                console.log('updated')
            }
            if (window.scrollY < 605 && this.state.scroll600 === true){
                this.setState({
                    scroll600: false
                })
            }
    }

    findLocationId = () => {
        return parseInt(this.props.location.pathname.slice(6))
    }

    findCurrentPost = () => {
        for (let i = 0; i < this.props.posts.articles.length; i++){
            if (this.props.posts.articles[i].ID === this.findLocationId()){
                return this.props.posts.articles[i]
            }
        }
    }

    render(){
        console.log('current post?', this.findCurrentPost())


        let thisPost = {}

        if(this.props.location.pathname === "/"){
            thisPost = this.props.posts.articles[0]
        } else {
            thisPost = this.findCurrentPost()
        }

        console.log(thisPost)

        if(thisPost){

            const backgroundImage = {  
                backgroundImage: `url(${thisPost.featured_image})`,
                backgroundPosition: 'center',
                backgroundSize: 'cover',
                backgroundRepeat: 'no-repeat'
            }
    
            const lockedMenu = {
                position: 'fixed',
                top: 0,
                width: '100%',
                // height: '65px',
                display: 'flex',
                flexDirection: 'row',
                justifyContent: 'space-evenly',
                // backgroundColor: 'rgba(255, 255, 255, 0.3)',

            }

            const unlockedMenu = {
                position: 'absolute',
                top: '605px',
                left: '0px',
                width: '100%',
                display: 'flex',
                flexDirection: 'row',
                justifyContent: 'space-evenly'
            }

            return (
                <div id="header" style={backgroundImage}>
                    <div id="headerMenu" style={this.state.scroll600 === false ? lockedMenu : unlockedMenu} ref={(ref) => this.scrollIcon = ref}>
                        <div class="socialMedia">
                            <a href="https://github.com/CBoetto">
                                <Tooltip message={'Github'} position={'bottom'}>
                                    <img src="/images/githubw.png" alt="social media" />
                                </Tooltip>
                            </a>
                        </div>
                        <div class="socialMedia">
                            <a href="https://www.instagram.com/art.from.beyond/">
                                <Tooltip message={'Instagram'} position={'bottom'}>
                                    <img src="/images/instagramw.png" alt="social media" />
                                </Tooltip>
                            </a>
                        </div>
                        <div class="socialMedia">
                            <a href="http://www.artfrombeyond.tumblr.com//">
                                <Tooltip message={'Tumblr Art Blog'} position={'bottom'}>
                                    <img src="/images/tumblrw.png" alt="social media" />
                                </Tooltip>
                            </a>
                        </div>
                        <div id="headerImage">
                            <img src="/images/headerLogo.png" alt="header" />
                        </div>
                        <div class="socialMedia">
                            <a href="http://www.calvinboetto.space">
                                <Tooltip message={'Portfolio'} position={'bottom'}>
                                    <img src="/images/portfoliow.png" alt="social media" />
                                </Tooltip>
                            </a>
                        </div>
                        <div class="socialMedia">
                            <a href="https://www.linkedin.com/in/cboetto/">
                                <Tooltip message={'Indeed'} position={'bottom'}>
                                    <img src="/images/linkedinw.png" alt="social media" />
                                </Tooltip>
                            </a>
                        </div>
                        <div class="socialMedia">
                            <Tooltip message={'cmitchellboetto@gmail.com'} position={'bottom'}>
                                <img src="/images/emailw.png" alt="social media" />
                            </Tooltip>
                        </div>
                    </div>
                    <div id="firstArticle" style={this.props.location.pathname !== "/" ? {width: '0px', overflow: 'hidden'} : null}>

                        <Link to={`/post/${thisPost.ID}`}>
                            <div>
                                <h1>{thisPost.title}</h1>
                                <h2  id="headerExcerpt" dangerouslySetInnerHTML={{__html: thisPost.excerpt}} />
                            </div>
                        </Link>
                    </div>
                </div>
            )
        } else {
            return <div></div>
        }
}
}

export default connect(mapStateToProps, null)(Header)