import React from 'react'
import { Link } from 'react-router-dom'

export default class RelatedPosts extends React.Component {
    

    render(){
        console.log('relatedpost', this.props.post)
        return (
            <div className="relatedPostsBox">
                <Link to={`/post/${this.props.post.ID}`}>
                    <img src={this.props.post.featured_image} alt="related post" />
                </Link>
                <h3>{this.props.post.title}</h3>
                <p dangerouslySetInnerHTML={{__html: this.props.post.excerpt}} />
                <hr></hr>
            </div>
        )
    }
}