import React from 'react'
import SubArticles from './subArticles'
import SubArticles2 from './subArticles2'
import Archive from './archive'
import {connect} from 'react-redux'

const mapStateToProps = state => {
    return {
        posts: state
    }
}

class FrontPage extends React.Component {

    render() {
        let subPosts = [];
        let subPosts2 = [];
        let allPosts = []
        if (this.props.posts !== undefined){
            subPosts = [this.props.posts.articles[1], this.props.posts.articles[2]]
            subPosts2 = [this.props.posts.articles[3], this.props.posts.articles[4], this.props.posts.articles[5]]
            allPosts = Object.values(this.props.posts.articles)
        }      
        if (this.props.posts.articles && this.props.posts.articles !== {}){

            return (
                <div id="frontPageContainer">
                    <div id="frontPageBottom">
                        <div id="featured"> 
                            {subPosts[0] ? 
                            <div id="subArticles">
                            {subPosts.map(post => <SubArticles post={post} key={post.ID} />)}
                            </div>
                            : <div></div>
                            }
                            <br />
                        </div>
                        {subPosts[0] ? 
                            <div id="subArticles2">
                            {subPosts2.map(post => <SubArticles2 post={post} key={post.ID} />)}
                            </div>
                            : <div></div>
                            }
                        <div id="archive">
                            <div id="archiveFont">
                                <h2>Archive</h2>
                            </div>
                            <ul>
                            {allPosts.map(post => <Archive post={post} key={post.ID} postNumber={allPosts.indexOf(post)} />)}
                            </ul>
                        </div>
                    </div>
                </div>
        );
        } else {
            return (
                <img src="http://www.calvinboetto.space//images/loading.gif" alt="loading" />
            )
        }
        }
    }

export default connect(mapStateToProps, null)(FrontPage)