import React from 'react'
import Tooltip from './tooltip'

export default class ArchiveCategories extends React.Component {

    render(){
        let category = ''
        if (this.props.props === 'Misc') {
            category = '/images/misc.png'
        } else if (this.props.props === 'Art') {
            category = '/images/art.png'
        } else if (this.props.props === 'Data Visualization') {
            category = '/images/data.png'
        } else if (this.props.props === '3D') {
            category = '/images/threed.png'
        } else if (this.props.props === 'Programming') {
            category = '/images/javascript.png'
        }
        console.log('reee', this.props)
        return(
            <li>
                <Tooltip message={this.props.props} position={'top'}>
                    <img src={category} alt="post category" />
                </Tooltip>
            </li>
        )
    }
}