import React from 'react'
import {Link} from 'react-router-dom'
import axios from 'axios'
import Header from './header'
import {connect} from 'react-redux'
import RelatedPosts from './relatedPosts'
import ArchiveCategories from './archiveCategories'

const mapStateToProps = state => {
    return {
        posts: state
    }
}

class Article extends React.Component {

    constructor(){
        super()
        this.findLocationId = this.findLocationId.bind(this)
        this.findCurrentPost = this.findCurrentPost.bind(this)
        this.findRelatedArticles = this.findRelatedArticles.bind(this)
        this.handleScroll = this.handleScroll.bind(this)

        this.state = {scroll600: false};


    }

    componentDidMount() {
        window.addEventListener('scroll', this.handleScroll);

    }

    componentWillUnmount(){
        window.removeEventListener('scroll', this.handleScroll);
      }

    handleScroll () {
            console.log(window.scrollY);
            if (window.scrollY > 543 && this.state.scroll600 === false){
                this.setState({
                    scroll600: true
                  });
                console.log('updated')
            }
            if (window.scrollY < 543 && this.state.scroll600 === true){
                this.setState({
                    scroll600: false
                })
            }
    }

    findLocationId = () => {
        return parseInt(this.props.location.pathname.slice(6))
    }

    findCurrentPost = () => {
        for (let i = 0; i < this.props.posts.articles.length; i++){
            if (this.props.posts.articles[i].ID === this.findLocationId()){
                return this.props.posts.articles[i]
            }
        }
    }

    findRelatedArticles = (postArr, currentCatArr) => {
        let relatedPosts = []
        for (let i = 0; i < postArr.length; i++){
            const categoryArr = Object.keys(postArr[i].categories)
            for (let j = 0; j <  categoryArr.length; j++){
                for (let x = 0; x < currentCatArr.length; x++){
                    if (categoryArr[j] === currentCatArr[x] && postArr[i] !== this.findCurrentPost()  && !relatedPosts.includes(postArr[i]) && relatedPosts.length < 2){
                        relatedPosts.push(postArr[i])
                    }
                }
            }
        }
        return relatedPosts
    }

    render(){

        const lockedRelated = {
            position: 'fixed',
            top: '60px',
            right: '5vw'
        }

        const unlockedRelated = {
            position: 'absolute',
            top: '605px',
            right: '5vw',
        }

        const currentPost = this.findCurrentPost()

        if (currentPost){

        let currentKeyArr = Object.keys(currentPost.categories)
        let allPostArr = Object.values(this.props.posts.articles)
        const relatedPosts = this.findRelatedArticles(allPostArr, currentKeyArr)

            return(
                <div className="articleContainer">
                    <div id="articleBack">
                        <Link to="/">
                            <h3>back</h3>
                        </Link>
                    </div>
                    <div id="middleArticleBox">
                        <div id="articleTitle">
                            <h1>{currentPost.title}</h1>
                            <div className="archiveCategories">
                                <ul>
                                    {currentKeyArr.map(category => <ArchiveCategories props={category} key={currentKeyArr.indexOf(category)} />) }
                                </ul>
                            </div>
                        </div>
                        <div id="articleBody" dangerouslySetInnerHTML={{__html: currentPost.content}} />
                    </div>
                    <div id="articleRelated" style={this.state.scroll600 === true ? lockedRelated : unlockedRelated}>
                    <div id="relatedTitleDiv">
                        <h2 style={{borderStyle: 'solid', borderWidth: '1px', width: '95px', padding: '5px'}}>Related:</h2>
                    </div>
                        {relatedPosts.map(post => <RelatedPosts post={post} />)}
                    </div>
                </div>
            )
        }
        return(
            <img src="http://www.calvinboetto.space//images/loading.gif" alt="loading" />
        )
    }
}

export default connect(mapStateToProps, null)(Article)