import axios from 'axios'

/**
 * ACTION TYPES
 */
const GET_POSTS = 'GET_POSTS'

/**
 * INITIAL STATE
 */
const defaultPosts = {}

/**
 * ACTION CREATORS
 */
const getPosts = posts => ({type: GET_POSTS, posts})

/**
 * THUNK CREATORS
 */

export const fetchPosts = () => async dispatch => {
  try{
    axios.get("https://public-api.wordpress.com/rest/v1.1/sites/frombeyondtheblog.wordpress.com/posts/")
    .then(res => {
      dispatch(getPosts(res.data.posts))
    })
  } catch (err) {
    console.log(err)
  }
}

/**
 * REDUCER
 */
export default function(state = defaultPosts, action) {
  switch (action.type) {
    case GET_POSTS:
      return action.posts
    default:
      return state
  }
}
